.shine {
    background: #f6f7f8;
    background-image: linear-gradient(to right, #f6f7f8 0%, #edeef1 20%, #f6f7f8 40%, #f6f7f8 100%);
    background-repeat: no-repeat;
    background-size: 800px 104px;
    display: inline-block;
    position: relative;

    -webkit-animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-name: placeholderShimmer;
    -webkit-animation-timing-function: linear;
}

.shimmer {
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 4em;
    margin: auto;
    padding: 0;
    display: inline;
}

.shimmer {
    text-align: center;
    color: rgba(255, 255, 255, 0.1);

    /*background: -webkit-gradient(linear, left top, right top, from(#222), to(#222), color-stop(0.5, #fff));*/
    /*background: -moz-gradient(linear, left top, right top, from(#222), to(#222), color-stop(0.5, #fff));*/
    /*background: gradient(linear, left top, right top, from(#222), to(#222), color-stop(0.5, #fff));*/
    background: -webkit-gradient(linear, left top, right top, from(#f100ff), to(#f100ff), color-stop(0.5, #fffa15));
    background: -moz-gradient(linear, left top, right top, from(#f100ff), to(#f100ff), color-stop(0.5, #fffa15));
    background: gradient(linear, left top, right top, from(#f100ff), to(#f100ff), color-stop(0.5, #fffa15));
    background-size: 125px 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    animation-name: shimmer;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    background-repeat: no-repeat;
    background-position: 0 0;

    background-color: #a418d7;
    /*background-color: #222;*/
}

@keyframes shimmer {
    0% {
        background-position: top left;
    }
    100% {
        background-position: top right;
    }
}
